.work {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

  &__view {
    display: flex;
    width: 80%;
    height: 80%;
    border-radius: 12px;
    box-shadow: 0 0 7px 1px rgba(0, 0, 0, 0.2);
    background: #fbfbfb;
  }

  &__settings,
  &__calendar {
    padding: 30px;
  }

  &__settings {
    width: 300px;
    border-right: 1px solid #e6e6e6;
  }

  &__calendar {
    width: 100%;
  }
}
